body {
  font-family: "Poppins";
  font-weight: 400;
}

h1, h2, h3, h4, h5 {
  font-family: "Poppins";
  font-weight: 700;
}

html {
  font-size: 100%;
} /* 16px */
h1 {
  font-size: 2.489rem; /* 39.84px */
}

h2 {
  font-size: 2.074rem; /* 33.12px */
}

h3 {
  font-size: 1.728rem; /* 27.68px */
}

h4 {
  font-size: 1.44rem; /* 23.04px */
}

h5 {
  font-size: 1.2rem; /* 19.2px */
}

small {
  font-size: 0.833rem; /* 13.28px */
}

header {
  padding: 1em;
  margin: 0 0 2em;
}
header a.men img {
  height: 80px;
}
header a img {
  height: 100px;
  width: auto;
}

nav {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1em;
}
nav p {
  font-weight: 600;
  font-size: 2rem;
  opacity: 0.3;
}

/*Desktop First*/
.container {
  max-width: 1536px;
  margin: 0 auto;
}

/*2xl*/
@media (max-width: 1536px) {
  .container {
    max-width: 1280px;
  }
}
@media (min-width: 1536px) {
  .container {
    max-width: 90%;
  }
}
/*xl*/
@media (max-width: 1280px) {
  .container {
    max-width: 1024px;
  }
}
/*lg*/
@media (max-width: 1024px) {
  .container {
    max-width: 768px;
  }
}
/*md*/
@media (max-width: 768px) {
  .container {
    max-width: 640px;
  }
}
/*sm*/
@media (max-width: 640px) {
  .container {
    max-width: 475px;
  }
}
/*xs*/
@media (max-width: 475px) {
  .container {
    width: 100%;
  }
}
#insight {
  display: flex;
  gap: 1.75em;
}
#insight > div {
  border: 2px solid var(--rich-black);
  padding: 0.6666666667em 1.75em;
  border-radius: 1em;
}

.table > div {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
}